'use client';
import { useEffect, useState } from 'react';

export default function TypingDemo() {
  const fullText = "我是三年三班的風紀股長\n你能幫我抓出同學的違規之處嗎？";
  const [chars, setChars] = useState([]);

  useEffect(() => {
    let i = 0;
    const interval = setInterval(() => {
      if (i < fullText.length) {
        setChars(prev => [...prev, fullText.charAt(i)]);
        i++;
      } else {
        clearInterval(interval);
      }
    }, 100);

    return () => clearInterval(interval);
  }, []);

  return (
    <div style={{ fontFamily: 'monospace', fontSize: 24 }}>
      {chars.map((char, idx) =>
        char === '\n' ? <br key={idx} /> : <span key={idx}>{char}</span>
      )}
    </div>
  );
}
